.ani-bg{
	display: inline-block;
    vertical-align: middle;
    box-shadow: 0 0 1px rgb(0 0 0 / 0%);
    overflow: hidden;

	margin: 0 6px;
	padding: 12px;
	cursor: pointer;
	background: #e1e1e1;
	text-decoration: none;
	color: #666;

	-webkit-tap-highlight-color: rgba(0,0,0,0);
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

/* fade */
.ani-bg-fade {
    
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;

    -webkit-transition-property: color, background-color;
    transition-property: color, background-color;
}
.ani-bg-fade:hover, .ani-bg-fade:focus, .ani-bg-fade:active {
	background-color: #2098D1;
	color: white;
}

/* Sweep To Right */
.ani-bg-sweep-to-right {
	position: relative;
	-webkit-transform: perspective(1px) translateZ(0);
	transform: perspective(1px) translateZ(0);
	-webkit-transition-property: color;
	transition-property: color;
	-webkit-transition-duration: 0.3s;
	transition-duration: 0.3s;
}
.ani-bg-sweep-to-right:before {
	content: "";
	position: absolute;
	z-index: -1;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: #2098D1;
	-webkit-transform: scaleX(0);
	transform: scaleX(0);
	-webkit-transform-origin: 0 50%;
	transform-origin: 0 50%;
	-webkit-transition-property: transform;
	transition-property: transform;
	-webkit-transition-duration: 0.3s;
	transition-duration: 0.3s;
	-webkit-transition-timing-function: ease-out;
	transition-timing-function: ease-out;
}
.ani-bg-sweep-to-right:hover, .ani-bg-sweep-to-right:focus, .ani-bg-sweep-to-right:active {
	color: white;
}
.ani-bg-sweep-to-right:hover:before, .ani-bg-sweep-to-right:focus:before, .ani-bg-sweep-to-right:active:before {
	-webkit-transform: scaleX(1);
	transform: scaleX(1);
}

